  created () {
    this.$emit('products', this.products)
  },
  mounted () {
    // 點擊搜尋放大鏡，內容從SearchArea.vue過來，在這裡filter()，再$emit發送給ShoppingMall.vue
    emitter.on('search', (searchText) => {
      const filterProducts = this.products.filter((v, i) => {
        return v.product_name.match(searchText)
      })
      this.$emit('products', filterProducts)
      // console.log(filterProducts)
    })
    emitter.on('modal_filter', (filter) => {
      const sequenceProducts = JSON.parse(JSON.stringify(this.products))
      let filterProducts = []
      // 排序：價格由低到高 or 由高到低
      if(filter.sequence === 'lowToHigh'){
        sequenceProducts.sort((a, b) => {
          return a.price - b.price
        })
      }else if(filter.sequence === 'highToLow'){
        sequenceProducts.sort((a, b) => {
          return b.price - a.price
        })
      }
      // 篩選：如果filter.filter陣列有其他東西時，進行篩選，結果輸入filterProducts
      if(filter.filter[0] !== 'none') {
        filterProducts = sequenceProducts.filter(v => filter.filter.includes(v.category))
      }
      // 輸出：如果filterProducts有東西就把它傳送出去，不然就傳送sequenceProducts
      if(filterProducts.length !== 0) {
        this.$emit('products', filterProducts)
      } else {
        this.$emit('products', sequenceProducts)
      }
    })
  }
}

</script>
